<template>
  <v-app>
    <rd-right-sidebar></rd-right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right dark>
          <v-breadcrumbs :items="dirs" class="breadcrumbs">
            <template v-slot:item="{ item }">
              <v-breadcrumbs-item class="breadcrumbs-item-text" :to="item.link"> {{ item.text }} </v-breadcrumbs-item>
            </template>
          </v-breadcrumbs>
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> Debug Messages </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-data-table
              v-model="selected"
              :headers="pointHeaders"
              :items="device.points"
              class="elevation-1"
              item-key="uuid"
              show-select
              :item-class="backgroundColor"
              :footer-props="tableProps.FOOTER_PROPS"
              :itemsPerPage="tableProps.ITEMS_PER_PAGE"
              :search="search"
            >
              <template v-slot:top >
                <v-toolbar flat>
                  <v-toolbar-title>POINTS</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <div class="text-left pa-2">
                    <v-btn @click="onClickRefresh" x-small color="primary" class="white--text" > Refresh<v-icon x-small right dark> mdi-refresh </v-icon> </v-btn>&nbsp;
                    <v-btn @click="dialog = true; tags = []" x-small color="success" class="white--text" > Create<v-icon x-small right dark> mdi-plus </v-icon> </v-btn>&nbsp;
                    <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text" > Delete<v-icon x-small right dark> mdi-delete </v-icon> </v-btn>&nbsp;
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(device.points, 'json')">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(device.points, 'excel')">Excel</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(device.points,'csv')">CSV</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>&nbsp;
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on" >Import<v-icon x-small right dark>mdi-import</v-icon></v-btn>
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="dialogImportExcel = true">Excel</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>&nbsp;
                    <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" > Export<v-icon x-small right dark> mdi-export </v-icon> </v-btn>&nbsp;
                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                </v-toolbar>
              </template>
              <template v-slot:item.created_on="{ item }" >
                {{item.created_on | timezone}}
              </template>
              <template v-slot:item.updated_on="{ item }" >
                {{item.updated_on | timezone}}
              </template>
              <template v-slot:item.ts_value="{ item }" >
                {{item.ts_value | timezone}}
              </template>
              <template v-slot:item.enable="{ item }">
                <v-checkbox v-model="item.enable" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:item.cov_threshold="{ item }">
                <v-edit-dialog :return-value.sync="item.cov_threshold" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.cov_threshold }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.cov_threshold" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.history_enable="{ item }">
                <v-checkbox v-model="item.history_enable" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:item.history_interval="{ item }">
                <v-edit-dialog :return-value.sync="item.history_interval" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.history_interval }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.history_interval" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.history_type="{ item }">
                <v-edit-dialog :return-value.sync="item.history_type" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.history_type }}</b>
                  <template v-slot:input>
                    <v-select v-model="item.history_type" :items="['COV', 'INTERVAL', 'COV_AND_INTERVAL']" ></v-select>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.input_min="{ item }">
                <v-edit-dialog :return-value.sync="item.input_min" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.input_min }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.input_min" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.input_max="{ item }">
                <v-edit-dialog :return-value.sync="item.input_max" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.input_max }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.input_max" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.scale_min="{ item }">
                <v-edit-dialog :return-value.sync="item.scale_min" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.scale_min }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.scale_min" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.scale_max="{ item }">
                <v-edit-dialog :return-value.sync="item.scale_max" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.scale_max }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.scale_max" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.value_round="{ item }">
                <v-edit-dialog :return-value.sync="item.value_round" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.value_round }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.value_round" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.value_operation="{ item }">
                <v-edit-dialog :return-value.sync="item.value_operation" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.value_operation }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.value_operation" single-line type='string'></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.writable="{ item }">
                <v-checkbox v-model="item.writable" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:item.tags="{ item }">
                <ul style="cursor: pointer;" @click="editItem(item)" class="overflow-td">
                  <li v-for="(key, index) of item.tags ? Object.keys(JSON.parse(item.tags)): []" :key="index">
                    {{key}} : {{JSON.parse(item.tags)[key]}}
                  </li>
                </ul>
              </template>
              <!-- <template v-slot:item.write_value="{ item }">
                <v-edit-dialog :return-value.sync="item.write_value" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.write_value }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.write_value" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template> -->
              <template v-slot:item.priority_array_write="{ item }" >
                <div class="overflow-td">
                  <span v-for="(key, index) of item.priority_array_write ? Object.keys(item.priority_array_write): []" :key="index" >
                    {{key}}:&nbsp;<span class="value">{{ item.priority_array_write[key] || item.priority_array_write[key] == 0  ? item.priority_array_write[key] : 'null' }}</span>,&nbsp;
                  </span>
                </div>
              </template>
              <template v-slot:item.disable_mqtt="{ item }">
                <v-checkbox v-model="item.disable_mqtt" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:item.actions="{ item }">
                <v-icon small class="mr-2" @click="editItem(item)" color="primary"> mdi-pencil </v-icon>
                <v-icon small @click="clickDelete(item)" color="error"> mdi-delete </v-icon>
              </template>
              <!-- headers -->
              <template v-slot:header.enable="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('enable') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.history_enable="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('history_enable') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('history_enable')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.history_interval="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('history_interval') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('history_interval')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.history_type="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('history_type') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('history_type')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.cov_threshold="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('cov_threshold') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('cov_threshold')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.input_min="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('input_min') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('input_min')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.input_max="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('input_max') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('input_max')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.scale_min="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('scale_min') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('scale_min')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.scale_max="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('scale_max') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('scale_max')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.value_operation="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('value_operation') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('value_operation')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.value_round="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('value_round') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('value_round')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.writable="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('writable') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('writable')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <!-- <template v-slot:header.write_value="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('write_value') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('write_value')" color="primary">mdi-pencil</v-icon>
                </div>
              </template> -->
              <template v-slot:header.tags="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('tags') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('tags'); tags = []" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.disable_mqtt="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('disable_mqtt') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('disable_mqtt')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:no-data> No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="2">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!-- Create/Edit dialog-->
    <v-dialog v-model="dialog" max-width="700px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field v-model="device.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-combobox
                    ref="comboBox"
                    @change="onChangeTypeName"
                    v-model="editedItem.name"
                    :items="typeNames"
                    label="Point name"
                    required
                    :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed',rules.required]">
                  </v-combobox>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-checkbox v-model="editedItem.enable" label="Enable" ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-checkbox v-model="editedItem.history_enable" label="History enable" ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-checkbox v-model="editedItem.writable" label="Writable" ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-checkbox v-model="editedItem.disable_mqtt" label="disable mqtt" ></v-checkbox>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-select v-model="editedItem.history_type" :items="['COV', 'INTERVAL', 'COV_AND_INTERVAL']" label="History type" ></v-select>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field v-model="editedItem.history_interval" label="History interval" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field v-model="editedItem.cov_threshold" label="Cov threshold" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field v-model="editedItem.value_operation" label="math operation" > </v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field v-model="editedItem.input_min" label="Input min" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field v-model="editedItem.input_max" label="Input max" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field v-model="editedItem.scale_min" label="Scale min" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field v-model="editedItem.scale_max" label="Scale max" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field v-model="editedItem.value_round" label="Value round" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col>
                <!-- <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.write_value" label="Write Value" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col> -->
                <v-col cols="12" sm="12" md="12">
                  <v-text-field  value="Tags" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <rd-point-tags :on-change="onChangeTag" :items="tags"></rd-point-tags>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid" > Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport" >
      <v-toolbar flat dark >
          <v-toolbar-title>Import Dialog</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon dark @click="closeDialogImport" >
              <v-icon>mdi-close</v-icon>
          </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1" > Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2" > Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport" ></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="pointHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)" v-if="!isClickSaveImport"> mdi-delete </v-icon>
                  <v-icon small color="error" v-if="isClickSaveImport && !isExistingvalue(item.uuid, 'uuid')">mdi-alert-octagram-outline</v-icon>
                  <v-icon small color="success" v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="createPoint(item)" v-if="isClickSaveImport" :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.name="{ item }" >
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" v-model="item.name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.name" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </template>
                <template v-slot:item.priority_array_write="{ item }">
                  {{item.priority_array_write && typeof item.priority_array_write == 'object' ? JSON.stringify(item.priority_array_write) : item.priority_array_write}}
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog :is-open="dialogImportExcel" :on-close="()=>dialogImportExcel = false" :on-confirm="convertExcelToJson"></import-from-excel-dialog>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <rd-point-tags v-if="editField === 'tags'" :on-change="onChangeTag" :items="tags"></rd-point-tags>
          <v-select v-if="editField === 'history_type'" v-model="editAll[editField]" :items="['COV', 'INTERVAL', 'COV_AND_INTERVAL']" :label="toUpperTextKey(editField)" ></v-select>
          <v-text-field v-if="editField === 'value_operation'" v-model="editAll[editField]" :items="value_operation" :label="toUpperTextKey(editField)" ></v-text-field>
          <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
          <v-text-field v-if="typeof editAll[editField] === 'number'" v-model.number="editAll[editField]" min="0" :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
          <v-text-field v-if="
            typeof editAll[editField] === 'string' &&
            editField !== 'history_type' &&
            editField !== 'value_operation' &&
            editField !== 'tags'"
            v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import { isJsonString, copyToClipboard, exportJSON, exportCSV, exportExcel, toUpperTextKey, convertPointsValueToString } from "../../utils/utils";
import axios from "../../axios";
import * as xlsx from "xlsx";
import _cloneDeep from 'lodash/cloneDeep';
import _find from 'lodash/find';
import RdPointTags from './RdPointTags'
import MqttConsole from '../Mqtt/MqttConsole';
import RdRightSidebar from './RdRightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import * as changeCase from "change-case";
import { getTypeNames, getTypeTags } from '../../utils/tagsUtils';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';

export default {
  components: {
    RdPointTags, MqttConsole, RdRightSidebar, ConfirmDialog, ImportFromExcelDialog,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    topic: 'rubix/points/debug',
    tab: 0,
    objectKeys: Object.keys,
    objectValues: Object.values,
    editedIndex: -1,
    action: "",
    device: {},
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    tags: [],
    pointHeaders: [
      { text: "Actions", value: "actions" , sortable: false},
      { text: "Name", value: "name", width: "90px",},
      { text: "Driver", value: "driver", sortable: false},
      { text: "Enable", value: "enable", sortable: false},
      { text: "History enable", value: "history_enable", sortable: false},
      { text: "History type", value: "history_type", sortable: false},
      { text: "History interval", value: "history_interval", sortable: false},
      { text: "COV threshold", value: "cov_threshold", sortable: false},
      { text: 'Input max', value: 'input_max', sortable: false},
      { text: 'Input min', value: 'input_min', sortable: false},
      { text: 'Scale max', value: 'scale_max', sortable: false},
      { text: 'Scale min', value: 'scale_min', sortable: false},
      { text: "Value round", value: "value_round", sortable: false},
      { text: "Value operation", value: "value_operation", sortable: false},
      { text: "Writable", value: "writable", sortable: false},
      { text: 'Disable mqtt', value: 'disable_mqtt', sortable: false},
      { text: "Priority Array Write", value: "priority_array_write", sortable: false},
      { text: "Fault", value: "fault", sortable: false},
      { text: "Value", value: "value", sortable: false},
      { text: "Value original", value: "value_original", sortable: false},
      { text: "Tags", value: "tags", sortable: false, width: "150px" },
      { text: 'Last value change timestamp', value: 'ts_value', sortable: false},
      { text: "Created on", value: "created_on", sortable: false},
      { text: "Updated on", value: "updated_on", sortable: false},
      { text: "Device uuid", value: "device_uuid", sortable: false},
      { text: "UUID", value: "uuid", align: "start" , sortable: false},
    ],
    value_operation: "x + 0",
    currentItem: {},
    editedItem: {
      name: "",
      enable: true,
      history_enable: true,
      history_type: "INTERVAL",
      history_interval: 15,
      cov_threshold: 1,
      input_max: null,
      input_min: null,
      scale_min: null,
      scale_max: null,
      value_operation: 'x + 0',
      writable: true,
      disable_mqtt: true,
      // write_value: 0,
      tags: "",
    },
    defaultItem: {
      name: "",
      enable: true,
      history_enable: true,
      history_type: "INTERVAL",
      history_interval: 15,
      cov_threshold: 1,
      input_max: null,
      input_min: null,
      scale_min: null,
      scale_max: null,
      value_operation: 'x + 0',
      writable: true,
      disable_mqtt: true,
      // write_value: 0,
      tags: "",
    },
    rules: {
      required: (value) => !!value || 'Required.',
    },
    dataImport: "",
    importArray: [],
    dialogImport: false,
    dirs: [],
    e1: 1,
    dialogImportExcel: false,
    search:"",
    searchWizard:"",
    dialogMassEdit: false,
    editAll: {
      cov_threshold: 0,
      enable: false,
      history_enable: false,
      history_interval: 0,
      history_type: "INTERVAL",
      input_max: 0,
      input_min: 0,
      scale_min: 0,
      scale_max: 0,
      value_round: 0,
      value_operation: 'x + 0',
      writable: false,
      disable_mqtt: true,
      // write_value: 0,
      tags: [{}],
    },
    editField: "",
    intervalId: null,
    isClickSaveImport: false,
    typeNames: getTypeNames(),
    tableProps: {...DATA_TABLE_PROPS},

  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Create Point" : "Edit Point";
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.driverRubix;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    tags(val) {
        this.onChangeTag(val);
    },
    '$route.params.id':function(val, oldVal){
      if(val != oldVal){
        this.getData();
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).finally(() => {
      next();
    });
  },
  mounted() {
    this.getData();

    //set auto refresh
    if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getData();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    onChangeTag(tags) {
      let strTags = '';
      if (Array.isArray(tags)) {
        const objTags = {};
        for (const tag of tags) {
          if (tag.key && tag.value) {
            objTags[tag.key] = tag.value;
          }
        }
        strTags = JSON.stringify({...objTags});
      } else {
        strTags = JSON.stringify(tags);
      }
      this.editAll[this.editField] = strTags;
      this.editedItem.tags = strTags;
    },
    async getData() {
      this.device = await axios.get(`point-server/generic/device/${this.$route.params.id}?with_children=true`)
      this.getDir();
      this.editedItem.device_uuid = this.defaultItem.device_uuid = this.device.uuid;
      this.device.points = this.device.points.map((item) => {
        const keys = this.objectKeys(item.point_store);
        if (keys && keys.length > 0) {
          keys.forEach((key) => {
            item = { ...item, [key]: item.point_store[key] };
          });
        }
        delete item.point_store;
        delete item.point_uuid;
        return item;
      });
    },
    getDir(){
      axios.get(`point-server/generic/network/${this.device.network_uuid}`).then((data)  => {
        this.dirs = [{text: 'NETWORKS', link: '/rubix-driver/networks'}],
        this.dirs.push({text: `NET: ${data.name}`, link: `/rubix-driver/networks/${data.uuid}/devices`});
        this.dirs.push({text: `DEV: ${this.device.name}`});
      });
    },
    editItem(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      const arrTags = [];
      if (item.tags) {
          const tags = JSON.parse(item.tags);
          for (const tag in tags) {
              arrTags.push({key: tag, value: tags[tag]});
          }
      }
      this.tags = arrTags;
      this.dialog = true;
    },
    save() {
      if(this.$refs.form.validate()){
        this.$refs.comboBox.blur();
        this.$nextTick(() => {
          this.editedItem.name = changeCase[this.setting.caseType](this.editedItem.name);
          if (this.editedIndex > -1) {
            this.updatePoint(this.editedItem);
          } else {
            this.createPoint(this.editedItem);
          }
        })

      }
    },
    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.device.points.indexOf(this.currentItem);
      this.device.points.splice(this.editedIndex, 1);
      await axios.delete(`point-server/generic/point/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false,
      this.$nextTick(() => {
        this.dataImport = "";
      });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if(this.importArray && this.importArray.length > 0){
        if(this.$refs.form.validate()){
          this.isClickSaveImport = true;
          try{
            const promises = [];
            for (const item of this.importArray) {
              item.device_uuid = this.device.uuid;  //update to the current device_uuid
              if(item.priority_array_write && typeof item.priority_array_write == 'string'){
                item.priority_array_write = JSON.parse(item.priority_array_write);
              }
              if(!item.uuid){
                promises.push(new Promise((resolve, reject) => this.createPoint(item, resolve, reject)));
              }
            }
            await Promise.all(promises);
            this.closeDialogImport();
            this.selected = [];
          }catch(ex){
          }
        }
      }
    },
    clickImportJson(){
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : "";
      this.e1 = 1;
    },
    convertExcelToJson(file){
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = (event)=>{
        let data = event.target.result;
        let workbook = xlsx.read(data,{type:"binary"});
        workbook.SheetNames.forEach(sheet => {
            let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
            const items = rowObject.map((i) => {
              for(let key in i){
                const newKey = key.trimRight("");
                const value = i[key];
                delete i[key];
                i = {...i, [newKey]: value}
              }
              i.device_uuid = this.device.uuid;
              return i;
            });
            this.dataImport = JSON.stringify(items);
            this.dialogImportExcel = false;
            this.dialogImport = true;
            this.e1 = 1;
        });
      }
    },
    clickContinue(){
      if(!isJsonString(this.dataImport) ){
        return this.$toasted.show("incorrect data type!", { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(point=>{
        delete point.uuid;
        return point;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      let points = _cloneDeep(this.device.points);
      if(this.editedIndex !== -1){ //when edit
        points.splice(this.editedIndex, 1);
      }
      return _find(points, {[fieldName]: value}) != null;;
    },
    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    clickDownload(array, type){
      // const data = JSON.parse(JSON.stringify(array)); //clone deep
      // data.forEach(point=>{
      //   if(point.priority_array_write){
      //     point.priority_array_write = `"${JSON.stringify(point.priority_array_write).replace("\"", "\'")}"`;
      //   }
      // });

      const data = convertPointsValueToString(JSON.parse(JSON.stringify(array)));
      if(type === "csv"){
        return exportCSV("points", data);
      } if(type === "excel"){
        return exportExcel("points", data);
      } if(type === "json"){
        return exportJSON("points", JSON.stringify(data));
      }
    },
    updatePoint(data) {
      // this.$refs.comboBox.blur();
      // this.$nextTick(() => {
      //   data.name = changeCase[this.setting.caseType](data.name);
      //   axios.put(`point-server/generic/point/${data.uuid}`, data )
      //     .then((response) => {
      //       const index = this.device.points.findIndex(n=> n.uuid == response.uuid);
      //       Object.assign(this.device.points[index], response);
      //       this.close();
      //       this.$toasted.show('update success');
      //     })
      //     .catch((error) => { this.$toasted.show('update fail', {type: 'error'}); });
      // })
      data.name = changeCase[this.setting.caseType](data.name);
      axios.put(`point-server/generic/point/${data.uuid}`, data )
        .then((response) => {
          const index = this.device.points.findIndex(n=> n.uuid == response.uuid);
          Object.assign(this.device.points[index], response);
          this.close();
          this.$toasted.show('update success');
        })
        .catch((error) => { this.$toasted.show('update fail', {type: 'error'}); });

    },
    async createPoint(data, resolve, reject) {
      try {
        data.name = changeCase[this.setting.caseType](data.name);
        const response = await axios.post(`point-server/generic/point`, data);
        this.device.points.push(response);
        if (this.importArray && this.importArray.length > 0) {
          const index = this.importArray.findIndex(n => n.name == response.name);
          if(index > -1){
            Object.assign(this.importArray[index], response);
          }
        }
        this.close();
        this.$toasted.show('create success');
        if (resolve) {
          resolve();
        }
      } catch (e) {
        if (reject) {
          reject();
        }
      }
    },
    backgroundColor(item) {
      let classColor = '';
      if(item.fault){
        classColor = 'background-fault-true';
      }else{
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor + ' set-row-height';
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.refresh();

    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh(){
      // this.selected = [];
      this.editAll = {
        cov_threshold: 0,
        enable: false,
        history_enable: false,
        history_interval: 0,
        history_type: "INTERVAL",
        input_max: 0,
        input_min: 0,
        scale_min: 0,
        scale_max: 0,
        value_operation: 'x + 0',
        value_round: 0,
        writable: false,
        disable_mqtt: true,
        tags: [{}],
        // write_value: 0,
      }
      this.editField = "";
      this.tags = [];
      this.dialogMassEdit = false;
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit() { },
    closeEdit() { },
    onClickRefresh(){
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    onChangeTypeName(val) {
      if(!this.typeNames.includes(val)) return;
      const arrTags = this.tags = [];
      const typeTags = getTypeTags(val);
      for (const tag of typeTags) {
        arrTags.push({key: tag, value: tag});
      }
      this.tags = arrTags;
    },
  },
};
</script>
